.post__buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 0;
}

.post__button {
    text-align: center;
    display: flex;
    align-items: center;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
}

.post__button h3 {
    font-size: 12px;
    margin-left: 5px;
    font-weight: 700;
    color: #777;
}

.post__button:hover {
    background-color: #eee;
}

.post__body {
    margin-top: 15px;
    border-top: 1px solid #ccc;
    padding-top: 10px;
}